.sneakers {
	&-title {
		display: flex;
		justify-content: space-between;
		align-items: center;

		h5 {
			font-family: var(--font-family);
			font-weight: 700;
			font-size: 32px;
			color: #000;
		}
	}

	&-pagination {
		padding-bottom: 40px;
	}

	&-input {
		position: relative;

		&-search {
			position: absolute;
			top: 15px;
			left: 18px;

		}

		&-close {
			position: absolute;
			width: 35px;
			height: 35px;
			top: 8px;
			right: 10px;
			cursor: pointer;
		}

		input {
			border: 1px solid #cacaca;
			border-radius: 10px;
			width: 550px;
			padding: 15px 0;
			padding-left: 46px;


			font-family: var(--font-family);
			font-weight: 400;
			font-size: 14px;
			color: rgb(97, 97, 97);
		}
	}


&-wrapper {
	padding-top: 40px;
		padding-bottom: 40px;
		
}

&-wrapper-skeleton {
	display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 40px;
}

	&-content {
		
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 40px;
	}

	&-card {
		position: relative;
		padding: 30px;
		padding-top: 0;
		border: 1px solid #cacaca;
		border-radius: 40px;
		// width: 210px;
		// height: 260px;
		background: #fff;

		h4 {
			font-family: var(--font-family);
			font-weight: 400;
			font-size: 26px;
			color: #000;
			padding-top: 14px;
			padding-bottom: 14px;
			max-width: 266px;
		}

		h6 {
			font-family: var(--font-family);
			font-weight: 500;
			font-size: 16px;
			text-transform: uppercase;
			color: #bdbdbd;
			padding-bottom: 2px;
		}

		h2 {
			font-family: var(--font-family);
			font-weight: 700;
			font-size: 20px;
			color: #000;
		}

		&-bottom {
			display: flex;
			// gap: 40px;
			justify-content: space-between;
			align-items: center;

			&-bookmarks {
				&-true {
					position: absolute;
					top: 30px;
					left: 30px;

					border: 1px solid #FF8585;

					border-radius: 20px;
					width: 50px;
					height: 50px;
					background: #fff;

					cursor: pointer;

					svg {
						width: 25px;
						height: 25px;
						fill: #FF8585;
					}

					&:hover {
						border: 1px solid #cacaca;
						transition: 0.3s all;

						.sneakers-card-bottom-bookmarks-svg {
							path {
								fill: #FF8585;
								transition: 0.3s all;

							}
						}
					}
				}

				&-false {
					position: absolute;
					top: 30px;
					left: 30px;

					border: 1px solid #cacaca;
					border-radius: 20px;
					width: 50px;
					height: 50px;
					background: #fff;

					cursor: pointer;

					svg {
						width: 25px;
						height: 25px;
						fill: #e7e5e5;
					}

					&:hover {
						border: 1px solid #FF8585;
						transition: 0.3s all;

						.sneakers-card-bottom-bookmarks-svg {
							path {
								fill: #FF8585;
								transition: 0.3s all;

							}
						}
					}
				}
			}

			&-btn {
				border: 1px solid #cacaca;
				border-radius: 20px;
				width: 50px;
				height: 50px;
				background: linear-gradient(180deg, #89f09c 0%, #3cc755 100%);
				cursor: pointer;

				svg {
					width: 20px;
					height: 20px;
				}

				&:hover {
					border: 1px solid #8bb43c;

					background: white;
					transition: 0.3s all;

					path {
						fill: #D3D3D3;
						transition: 0.3s all;
					}
				}

				&-true {
					
					border-radius: 20px;
					width: 50px;
					height: 50px;
					background: #fff;

					cursor: pointer;

					&:hover {
						border: 1px solid #cacaca;
						transition: 0.3s all;

						path {
							fill: #8bb43c;
							transition: 0.3s all;
						}
					}

					border: 1px solid #8bb43c;
					

						path {
							fill: #8bb43c;
							transition: 0.3s all;
						}

					svg {
						width: 20px;
						height: 20px;
					}

				}

				&-false {
					border: 1px solid #cacaca;
					border-radius: 20px;
					width: 50px;
					height: 50px;
					background: #fff;

					cursor: pointer;

					&:hover {
						border: 1px solid #8bb43c;
						transition: 0.3s all;

						path {
							fill: #8bb43c;
							transition: 0.3s all;
						}
					}

					svg {
						width: 20px;
						height: 20px;
					}

					
				}
			}
		}
	}
}